
.parall{
    margin-top: 2vh;
    height: 500px;
}
.container{
    margin-top: 20vh;
    font-size: 100px;
    
    margin-right: 10px;
}

@media screen and (min-width:700px)and (max-width:1200px)
{
    .content{
        width: 50%;
    }
    .parall{
        height: 400px;
        width: 100%;
    }
    .container{
        margin-top: 2vh;
    }
}

@media (max-width:400px){
    .parall{
        height: 300px;
        width: 100%;
    }
    .container{
        font-size: 50px;
        
    }
    
}
