.body{
    height: 70vh;
    background-color: #fff;
}
.bigcard{
    height: 50vh;
    margin: 20px;
    background-color: #fff;
}
.client{
    text-align: center;
}
.contenta::before{
    content: "\201c";
    font-size: 70px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: lightgray;
    margin: 1vh;
    
}
.contenta::after{
    margin:35vh;
    content: "\201d";
    font-size: 70px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: lightgrey;
    
}
.card{
    padding: 10px 5px;
    margin-top: 2%;
    width: 50vh;
    height: 30vh;
    background-color: azure;
}
.contenta{
    margin-left: 1vh;
    text-align: left;
}
.nothing{
    display: none;
}
.photo{
    margin-top: -8vh;
    width: 150px;
    clip-path: circle();
}
.CName{
    margin-top: 0vh;
    margin-left: 2vh;
    font-size: 15px;
    color: black;
}
@media (max-width:400px){
    .body{
        height: 70vh;
    }
    .client{
        font-size: 30px;
    }
    .bigcard{
        height: 50vh;
    }
    .card{
        height: 30vh;
        width: 45vh;
    }
    .photo{
        margin-top: -18vh;
    }
}