


.blog{
    display: flex;
    flex-direction: column;
    background-color: #fff;
}
.up{
    flex: 0.5;
    display: flex;
}
.down{
    flex: 0.5;
    display: flex;
}
.left{
    flex:0.5;
}
.right{
    flex: 0.5;
}
.left1{
    flex: 0.5;
}
.right1{
    flex: 0.5;
}
.name{
    margin-left: 10vh;
    
}
.h{
    font-size: 50px;
    padding: 0;
    margin-bottom: 0.5%;
    text-align: left;
    margin-top: 5vh;
    font-weight:bolder;

    font-weight: 700px;
    color: green;
    
    
}
.line{
    display: flex;
    width: 50px;
    height: 50px;
    margin-left: 10vh;
    margin-top: 0;
    
}
span{
    
    width: 140%;
    
    height: 6px;
    background-color: #000;
    
}
.p1{
    text-align: left;
    margin-top: -4vh;
    margin-left: 7vh;
    font-size: 25px;
    color: #000;
}
.p2{
    font-size: 20px;
    color: #000;
    text-align: left;
    margin-left: 7vh;
    opacity: 0.7;
    
}
.images1{
    display: flex;
    justify-content:space-around;
    margin-right: 2vh;
}
.images2{
    display: flex;
    justify-content: space-around;
    margin-left: 2vh;
}
.imgcontainer1{
    flex: 0.49;
    
    margin-top: 5vh;
    width: 450px;
    height: 350px;
}
.imgcontainer2{
    flex: 0.49;
    margin-top: 5vh;
    width:450px;
    height:350px;
}
.imgcontainer3{
    flex: 0.49;
    width: 450px;
    height:350px;
}
img{
    width: 100%;
}
.imgcontainer4{
    flex: 0.49;
    width:450px;
    height:350px;
}
.h2{
    font-size: 50px;
    font-weight: bolder;
    padding: 0;
    margin-bottom: 1%;
    text-align: left;
    margin-top: 10vh;
    margin-left: 5vh;

    font-weight: 700px;
    color: green;
}
.para1{
    margin-left: 5vh;
    text-align: left;
    font-size: 25px;
}

