@media (max-width: 400px){
    
    
    
    .up{
         flex-direction: column;
         text-align: center;
         width: auto;
         align-items: center;
     }
     .images1{
         flex-direction: column;
         align-items: center;
         margin-right: 0;
         margin-left: 1vh;
     }
     .imgcontainer1{
        align-items: center;
        width: 90%;
        height: auto;
    }
    .images2{
        flex-direction: column;
        align-items: center;
        margin-right: 0;
        margin-left: 1vh;
    }
    .imgcontainer2{
       align-items: center;
       width: 90%;
       height: auto;
   }
   .imgcontainer3{
       display: none;
   }
   .imgcontainer4{
       display: none;
   }
   .name{
       margin-left: 4vh;
   }
   
   h1{
       color: #000;
   }
   h2{
       margin-top: 5vh;
   }  
   .down{
         flex-direction: column;
   }
   .images2{
         flex-direction: column;
   }
   .p1,.p2,.para1{
       margin:0 4vh 0 4vh;
   }
   .p1{
       margin-top:-4vh ;
   }
   .name2{
       margin-left: 4vh;
   }
   
}

