.imagecontainer{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 60vh;
    margin-left: 0vh;
    
    
    
}
.containera{
    position: relative;
    text-align: center;
}
.text{
    position: absolute;
    top: 90%;
    left: 40%;
    transform: translate(-50%,-50%);
    z-index: 1000;
    font-size: 60px;
    color: #000;

}
.band{
    position: absolute;
    top: 90%;
    left: 40%;
    transform: translate(-50%,-50%);
    z-index: 1000;
    font-size: 60px;
    color: #000;
    opacity: 0.9;
}
.text{
    position: absolute;
    
    left: 20%;
    transform: translate(-50%,-50%);
    z-index: 1000;
    font-size: 60px;
    color: #fff;

}

.band{
    display: flex;
    width: 120%;
    height: 50px;
    
    margin-top: 0;
    
}
.black{
    
    width: 140%;
    height: 60px;
    background-color: #000;
    
}
.menu{
    display: flex;
}
.heading{
    margin: 10px 5px 0 350px ;
    font-size: 20px;
    text-align: left;
    flex:0.3;
    color: green;
}
.tnp{
    flex:0.5;
    display: flex;
    margin-top: 14px;
    
}
.type{
    flex: 0.5;
    margin-top: -3.5%;
}
.tex{
    text-align: left;
    font-size: 15px;
    margin-left: -5vh;
    margin-top: 15%;
}
.pric{
    flex: 0.4;
    margin-top: 15%;
}
@media screen and (min-width:500px) and (max-width:1400px){
      .imagecontainera{
          height: 150px;
          width: 300px;
      }    
}
@media (max-width:400px)
{
     .imagecontainer{
         width: fit-content;
     }
     .text{
         font-size: 25px;
         margin-left: 2vh;
     }
     .menu{
         display: flexbox;
         flex-direction: column;
         flex: 1;
         margin-right: 20px;
     }
     .tex{
         margin-top: auto;
     }
     .pric{
         margin-top: 20%;
     }
     .heading{
         flex: 1.0;
         margin:1vh 0 -5vh 3vh;
    }
     .tnp{
         flex: 1.0;
         display: flex;
         margin-left: 8vh;
     }
     .tex{
         flex: 0.5;
     }
     .price{
        margin-top: -2vh; 
        flex: 0.5;
     }
}